const hamburgerStyle = {

  bmBurgerButton: {
    position: 'fixed',
    width: '24px',
    height: '20px',
    right: '2rem',
    top: '1.7rem'
  },

  bmBurgerBars: {
    background: '#64FFDA'
  },

  bmCrossButton: {
    height: '24px',
    width: '24px',
    right: '2rem'
  },

  bmCross: {
    marginRight: '2rem',
    height: '24px',
    background: 'rgba(0, 0, 0, .8)'
  },

  bmMenu: {
    background: '#64FFDA',
    padding: '2.5em 1.5em 0',
    fontSize: '1em'
  },

  bmMorphShape: {
    fill: '#373a47'
  },

  bmItemList: {
    color: '#b8b7ad',
    padding: '0.8em'
  },

  bmOverlay: {
    background: 'rgba(0, 0, 0, 0.6)',
    zIndex: '1',
    left: '0'
  }
};

export default hamburgerStyle;



